// $text-white : #a5a5a5;
$text-dark : #403F3D;
$text-light : #E1D4C9;
$text-accent : #B0907A;
$bg-body : #E1D4C9;
$bg-container : #665F55;
$bg-backdrop : rgba(64, 63, 61, 0.80);
$border-light : #C1B6AD;
$border-dark : #665F55;
@mixin mainFont($fontSize) {
    font-size: $fontSize;
    font-weight: 400;
}

@mixin heading-1() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 72px;
    font-style: normal;
    font-weight: 600;
    line-height: 105%;
}

@mixin heading-1-accent() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 72px;
    font-style: italic;
    font-weight: 600;
    line-height: 105%;
}

@mixin heading-2() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 60px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
}

@mixin heading-2-accent() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 60px;
    font-style: italic;
    font-weight: 600;
    line-height: 125%;
}

@mixin heading-3() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
}

@mixin body-medium() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
}

@mixin body-caption() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
}

@mixin action-link-button() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%;
}

@mixin action-burger-link() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
}

.mobile {
    margin-bottom: 100px;
}

.mobile_wrapper {
    display: flex;
    justify-content: center;
    gap: 100px;
}

.mobile_left_part,
.mobile_right_part {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.mobile_left_part {
    width: 50%
}

.mobile_right_part {
    width: 50%
}

.mobile_content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 40px;
    height: 100%;
}

.mobile_title {
    @include heading-2;
}

.mobile_word {
    @include heading-2-accent;
    color: $text-accent;
}

.mobile_desription {
    @include body-medium;
}

.mobile_buttons {
    display: flex;
    gap: 20px;
}

.svg_path path {
    transition: all 0.4s linear;
}

.mobile_btn {
    padding: 12px 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    border-radius: 100px;
    border: 1px solid $border-dark;
    transition: all 0.3s linear;

    &:active .svg_path path {
        fill: $text-light;
    }

    &:active {
        background-color: $bg-container;
    }

    &:active .mobile_btn_title,
    &:active .mobile_btn_text {
        color: $text-light;
    }
}

.mobile_btn_content {
    min-width: 114px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 2px;
}

.mobile_btn_title {
    transition: color 0.3s linear;
    @include body-caption;
}

.mobile_btn_text {
    transition: color 0.3s linear;
    @include action-link-button
}

.mobile_img {
    max-width: 630px;
    width: 100%;
    height: auto;
}

@media not all and (hover: none) {
    .mobile_btn:hover .svg_path path {
        fill: $text-light;
    }


    .mobile_btn:hover {
        background-color: $bg-container;
    }

    .mobile_btn:hover .mobile_btn_title,
    .mobile_btn:hover .mobile_btn_text {
        color: $text-light;
    }
}

@media screen and (max-width: 950px) {
    .mobile_wrapper {
        flex-direction: column;
    }

    .mobile_left_part {
        width: 100%;
    }

    .mobile_title {
        letter-spacing: -0.2px;
    }

    .mobile_right_part {
        width: 100%;
    }

    .mobile_wrapper {
        gap: 98px;
    }
}

@media screen and (max-width: 510px) {

    .mobile_wrapper {
        gap: 39px;
    }

    .mobile_title,
    .mobile_word {
        font-size: 32px;
    }

    .mobile_buttons {
        flex-direction: column;
        gap: 17px;
    }

    .mobile_btn {
        max-width: 200px;
    }
}