// $text-white : #a5a5a5;
$text-dark : #403F3D;
$text-light : #E1D4C9;
$text-accent : #B0907A;
$bg-body : #E1D4C9;
$bg-container : #665F55;
$bg-backdrop : rgba(64, 63, 61, 0.80);
$border-light : #C1B6AD;
$border-dark : #665F55;
@mixin mainFont($fontSize) {
    font-size: $fontSize;
    font-weight: 400;
}

@mixin heading-1() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 72px;
    font-style: normal;
    font-weight: 600;
    line-height: 105%;
}

@mixin heading-1-accent() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 72px;
    font-style: italic;
    font-weight: 600;
    line-height: 105%;
}

@mixin heading-2() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 60px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
}

@mixin heading-2-accent() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 60px;
    font-style: italic;
    font-weight: 600;
    line-height: 125%;
}

@mixin heading-3() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
}

@mixin body-medium() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
}

@mixin body-caption() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
}

@mixin action-link-button() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%;
}

@mixin action-burger-link() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
}

.about {
    margin-bottom: 100px;
}

.about_wrapper {
    display: flex;
    flex-direction: column;
    gap: 39px;
}

.abou_title {
    @include heading-2;
    font-size: 59px;
    line-height: 127%;
    letter-spacing: 0.4px;
}

.about_word {
    @include heading-2-accent;
    color: $text-accent;
    letter-spacing: 0px;
}

.about_imgs {
    display: flex;
    gap: 40px;
    width: 100%;
    height: auto;
}

.about_left,
.about_right {
    display: flex;
    flex-direction: column;
    gap: 40px;
    align-items: center;
}

.about_elem {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 660px;
    width: 100%;
    max-height: 590px;
    overflow: hidden;
    border-radius: 20px;

    &_min {
        max-height: 430px;
        height: auto;
    }
}

.about_img {
    max-width: 726px;
    width: 100%;
    height: auto;
    transform: scale(110%);
    transition: transform 0.5s linear;

    // &:hover {
    //     transform: scale(100%);
    // }
}

.about_min_img {
    max-height: 430px;
    width: auto;
}

@media not all and (hover: none) {
    .about_img:hover {
        transform: scale(100%);
    }
}

@media screen and (max-width: 768px) {
    .about_imgs {
        flex-direction: column;
    }

    .about_elem_min {
        display: none;
    }

    .about_elem {
        max-width: 100%;
    }

    .about_img {
        transform: scale(105%);
    }
}

@media screen and (max-width: 510px) {

    .abou_title,
    .about_word {
        line-height: 125%;
        letter-spacing: 0;
        font-size: 32px;
    }

    .about_elem {
        height: 590px;
    }

    .about_img {
        transform: scale(100%);
        width: auto;
    }
}