// $text-white : #a5a5a5;
$text-dark : #403F3D;
$text-light : #E1D4C9;
$text-accent : #B0907A;
$bg-body : #E1D4C9;
$bg-container : #665F55;
$bg-backdrop : rgba(64, 63, 61, 0.80);
$border-light : #C1B6AD;
$border-dark : #665F55;
@mixin mainFont($fontSize) {
    font-size: $fontSize;
    font-weight: 400;
}

@mixin heading-1() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 72px;
    font-style: normal;
    font-weight: 600;
    line-height: 105%;
}

@mixin heading-1-accent() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 72px;
    font-style: italic;
    font-weight: 600;
    line-height: 105%;
}

@mixin heading-2() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 60px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
}

@mixin heading-2-accent() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 60px;
    font-style: italic;
    font-weight: 600;
    line-height: 125%;
}

@mixin heading-3() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
}

@mixin body-medium() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
}

@mixin body-caption() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
}

@mixin action-link-button() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%;
}

@mixin action-burger-link() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
}

.footer {
    margin-bottom: 40px;
}

.footer_wrapper {
    display: flex;
    gap: 100px;
    padding: 100px;
    background-color: $bg-container;
    border-radius: 40px;
}

.footer_left {
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.footer_title {
    @include heading-2;
    color: $text-light;
}

.footer_word {
    @include heading-2-accent;
    color: $text-accent;
}

.footer_svg {
    cursor: pointer;
    transition: all 0.3s linear;
    fill: $bg-container;

    &:active {
        fill: $bg-body;
    }

    &:active path {
        stroke: $text-dark;
    }

}

.footer_svg path,
.footer_svg rect {
    transition: all 0.3s linear;
}


.footer_social {
    display: flex;
    gap: 12px;
}

.footer_right {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 36px;
}

.footer_contacts {
    @include heading-3;
    color: $text-light;
}

.footer_link,
.footer_link_date {
    @include action-link-button;
    color: $text-light;
}

.footer_contact_list {
    display: flex;
    flex-direction: column;
    gap: 18px;
}

.footer_link,
.footer_link_date {
    position: relative;
    display: inline;
}

.footer_link_text {
    margin-left: 6px;
}

.footer_link_date {
    cursor: auto;
}

.footer_border {
    &::after {
        position: absolute;
        top: 120%;
        left: 50%;
        z-index: 2;
        display: block;
        content: "";
        width: 0;
        height: 2px;
        background-color: $text-light;
        transition: all 0.5s linear;

        transform: translateX(-50%);
        opacity: 0;
    }
}

@media not all and (hover: none) {
    .footer_svg:hover {
        fill: $bg-body;
    }

    .footer_svg:hover path {
        stroke: $text-dark;
    }

    .footer_border:hover::after {
        width: 100%;
        opacity: 1;
    }
}

@media screen and (max-width: 1080px) {
    .footer_wrapper {
        flex-direction: column;
        padding: 100px 60px 103px;
        gap: 101px;
    }

    .footer_left,
    .footer_right {
        width: 100%;
    }

    .footer_right {
        gap: 35px;
    }

}

@media screen and (max-width: 560px) {

    .footer_wrapper {
        padding: 60px 16px 62px;
        gap: 42px;
    }

    .footer_title,
    .footer_word {
        font-size: 32px;
    }
}