// $text-white : #a5a5a5;
$text-dark : #403F3D;
$text-light : #E1D4C9;
$text-accent : #B0907A;
$bg-body : #E1D4C9;
$bg-container : #665F55;
$bg-backdrop : rgba(64, 63, 61, 0.80);
$border-light : #C1B6AD;
$border-dark : #665F55;
@mixin mainFont($fontSize) {
    font-size: $fontSize;
    font-weight: 400;
}

@mixin heading-1() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 72px;
    font-style: normal;
    font-weight: 600;
    line-height: 105%;
}

@mixin heading-1-accent() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 72px;
    font-style: italic;
    font-weight: 600;
    line-height: 105%;
}

@mixin heading-2() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 60px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
}

@mixin heading-2-accent() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 60px;
    font-style: italic;
    font-weight: 600;
    line-height: 125%;
}

@mixin heading-3() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
}

@mixin body-medium() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
}

@mixin body-caption() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
}

@mixin action-link-button() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%;
}

@mixin action-burger-link() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
}

.enjoy {
    margin-bottom: 100px;
}

.enjoy_wrapper {
    position: relative;
    padding: 100px;
    width: 100%;
    height: 644px;
    background: url('../../../img/enjoy-bg.jpg') center/cover no-repeat;
    border-radius: 40px;
    overflow: hidden;
}

.enjoy_inner {
    display: flex;
    flex-direction: column;
    max-width: 530px;
    width: 100%;
    gap: 41px;
}


.enjoy_video {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.enjoy_title {
    @include heading-1;
    color: $text-light;
    z-index: 3;
}

.enjoy_word {
    @include heading-1-accent;
    color: $text-accent;
    z-index: 3;
}

.enjoy_description {
    @include body-medium;
    color: $text-light;
    z-index: 3;
}

.enjoy_btn {
    display: flex;
    width: 200px;
    padding: 20px 0;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    background-color: $bg-body;
    @include action-link-button;
    gap: 8px;
    z-index: 3;

    &_text {
        transform: translateX(14px);
        transition: all 0.5s ease-in-out;
    }

    &_logo {
        width: 20px;
        height: 20px;
        opacity: 0;
        transition: all 0.4s linear;
    }
}

@media not all and (hover: none) {
    .enjoy_btn:hover .enjoy_btn_text {
        transform: translateX(0);
    }

    .enjoy_btn:hover .enjoy_btn_logo {
        width: 20px;
        opacity: 1;
    }
}

@media screen and (max-width: 800px) {
    .enjoy_wrapper {
        padding: 100px 60px;
    }

    .enjoy_btn_text {
        transform: translateX(0);
    }

    .enjoy_btn_logo {
        opacity: 1;
    }
}

@media screen and (max-width: 732px) {
    .enjoy {
        margin-bottom: 101px;
    }

    .enjoy_wrapper {
        padding: 60px 16px;
        height: 552px;
    }

    .enjoy_inner {
        gap: 40px;
    }

    .enjoy_title,
    .enjoy_word {
        line-height: 115%;
        font-size: 42px;
    }
}